<template>
    <div :class="[
        'cl-list',
        border && 'cl-list--border',
        size && `cl-list--${size}`,
        split && `cl-list--split`,
    ]">
        <div class="cl-list__header" v-if="header || $slots.header">
            <slot name="header">{{header}}</slot>
        </div>
        <div class="cl-list__content">
            <slot></slot>
        </div>
        <div class="cl-list__footer" v-if="footer || $slots.footer">
            <slot name="footer">{{footer}}</slot>
        </div>
        <cl-loading fix :visible="loading" />
    </div>
</template>

<script>

    export default {
        name: "ClList",
        props: {
            border: Boolean,
            hover: {
                type: Boolean,
                default: true
            },//hover
            split: {
                type: Boolean,
                default: true
            },//
            header: String,
            footer: String,
            loading: Boolean,
            type: {
                type: String,
                default: '',//meta
            },
            gutter: {
                type: [String, Number],
                default: 10
            },//list-rowitemtype
            labelWidth: {
                type: [String, Number],
                default: 100
            },//list-row
            size: {
                type: String,
                default: 'default',
                validator(value){
                    return ['mini', 'small', 'default', 'large'].includes(value)
                }
            }
        },
        data() {
            return {
                componentName: 'ClList',
                everyRowItemLen: [],//list-rowitem
                itemWidth: '0%'
            }
        },
        computed: {
        },
        components: {},
        created() {
        },
        mounted() {
            this.$on('on-update-item-width', (len) => {
                this.everyRowItemLen.push(len);
                let sortArr = this.everyRowItemLen.sort((a, b) => {
                    return a - b;
                }).reverse();
                if(sortArr && sortArr.length){
                    this.itemWidth = 100 / sortArr[0] + '%'
                }
            })
        },
        methods: {}
    }
</script>
